class PowerFileValidator{

    static PROJECT_OBJECT_TEMPLATE = {
        Project: {
          Tasks: {
            Task: []
          },
          Resources: {
            Resource: []
          },
          Assignments: {}
        }
      };

      static TASK_OBJECT_TEMPLATE = {
        UID: {
          _text: null,
        },
        Name:{
          _text: null,
        },
        Start: {
          _text: null,
        },
        Finish:{
          _text: null,
        },
        OutlineNumber:{
          _text: null,
        },
        OutlineLevel:{
          _text: null,
        },
        PercentComplete:{
          _text: null,
        }
      };

      static RESOURCE_OBJECT_TEMPLATE = {
        UID: {
          _text: null
        },
        Name: {
          _text: null
        }
      }

      static ASSIGNMENT_OBJECT_TEMPLATE = {
        TaskUID: {
          _text: null
        },
        ResourceUID: {
          _text: null
        },
        Work: {
          _text: null
        }
      }
      
      static isMsProjectStructure (template, checkedObject) {
        for (const key in template) {
      
          if (!template.hasOwnProperty(key) || !checkedObject.hasOwnProperty(key)) return false;
      
          if (typeof template[key] === "object" && Object.keys(template[key]).length) {
            this.msProjectStructure(template[key], checkedObject[key]);
          }
        }   
        return true;
      };

      static isXmlFile(file) {
       
        if (file) throw new Error("No files provided!");
      
        return "text/xml" === file.type;
      }


}
export default PowerFileValidator;